.mapboxgl-popup-content {
    background-color: var(--colorGreyPopup);
    display: grid;
    grid-gap: 4px 10px;
    grid: repeat(4, auto) / auto auto;
    padding: 10px 15px;
}

.mapboxgl-popup-content > *:nth-child(even) {
